.products {
	display: flex;
	overflow-x: scroll;
	overflow-y: hidden;
	gap: 3rem;
}

.product {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: var(--color-white);
	box-shadow: 0px 7.20692px 14.4138px rgba(0, 0, 0, 0.04);

	&__text {
		display: flex;
		flex-direction: column;
		gap: 1.1rem;
		text-align: center;
		padding: 2.174rem 0;
	}

	&__title {
		font-size: 2.4rem;
	}

	&__price {
		font-size: 1.6rem;
	}
}
