.top-screen {
	position: relative;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: center;

	&__main {
		width: 100%;
		height: auto;
		top: 0;
		left: 0;
	}

	&__skyski {
		position: absolute;
		width: 76.8rem;
		height: 23rem;
		left: 85rem;
		top: 18.7rem;
		transform: translateX(-50%);
		font-family: "Poppins";
		font-style: normal;
		font-weight: 700;
		font-size: 20rem;
		line-height: 140%;
		color: var(--color-secondary);
	}

	&__webuild {
		position: absolute;
		width: 74.8rem;
		height: 3.6rem;
		left: 85rem;
		top: 40rem;
		transform: translateX(-50%);
		font-family: "Poppins";
		font-style: normal;
		font-weight: 700;
		font-size: 3rem;
		line-height: 120%;
		color: var(--color-secondary);
	}

	&__line {
		position: absolute;
		width: 100%;
		height: 0rem;
		left: 0rem;
		top: 10.652rem;
		border: 1.33151px solid #ffffff;
	}

	&__button {
		display: flex;
		flex-direction: row;
		align-items: flex-start;
		padding: 2rem 4rem;
		gap: 1.4rem;
		position: absolute;
		width: 20.3rem;
		height: 8.7rem;
		left: 77.2rem;
		top: 58.2rem;
		background: var(--color-primary);
		color: var(--color-secondary);

		font-family: "Poppins";
		font-style: normal;
		font-weight: 700;
		font-size: 2.2rem;
		line-height: 120%;
	}
}

@media (max-width: 767px) {
	.top-screen {
		&__skyski {
			width: 40%; // Adjust the width to fit mobile screens
			height: auto; // Keep the aspect ratio
			left: 50%; // Adjust the position
			top: 30%; // Adjust the position
			font-size: 10rem; // Adjust the font size
		}

		&__webuild {
			width: 40%; // Adjust the width to fit mobile screens
			height: auto; // Keep the aspect ratio
			left: 50%; // Adjust the position
			top: 50%; // Adjust the position
			font-size: 2rem; // Adjust the font size
		}

		&__line {
			width: 100%; // Adjust the width to fit mobile screens
			height: 0rem;
			left: 0rem;
			top: 3rem; // Adjust the position
			border: 0.1rem solid #ffffff;
		}
	}
}

